.header{width:100%;height:72px;background:#0064eb;display:flex;align-items:center}.header .icon{font-size:40px;color:#fff;margin-left:41px}@media screen and (max-width: 600px){.header .icon{margin-left:10px}}.header_textbox{width:500px;height:40px;margin-left:22px;position:relative;display:flex;align-items:center;border-radius:6px;background-color:#fff}@media screen and (max-width: 600px){.header_textbox{width:calc(100% - 140px)}}.header_textbox_icon{margin:0 13px 0 19px}.header_textbox input{width:100%;max-width:calc(100% - 60px);height:100%;border-radius:6px;border:none;outline:none;font-family:"Inter";font-style:normal;font-weight:400;font-size:14px;line-height:17px;display:flex;align-items:center}